@import '../variables';

.pizza-block {
  width: 100%;
  text-align: center;
  margin-bottom: 65px;

  &__top{
    position: relative;
    &:hover{
      p{
        display: block;
      }
    }

  }
  &__prompt{
    position: absolute;
    left: 50%;
    top: 20%;
    transform: translate(-50%, 50%);
    background-color: #fff;
    padding: 20px 30px;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 6px;
    display: none;
    opacity: 0.9;
  }

  &__image {
    width: 260px;
    // &:hover{
    //   box-shadow: 2px 2px 4px 4px rgba(0, 0, 0, 0.4);
    // }
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();

        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}